<template>
  <div>
    <mt-header :title="title">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <input
      type="button"
      title="开启摄像头"
      value="开启摄像头"
      @click="getMedia"
    />
    <video id="video" width="200px" height="100px" autoplay="autoplay"></video>
    <canvas id="canvas" width="200px" height="100px"></canvas>
    <button id="snap" @click="takePhoto">拍照</button>
  </div>
</template>

<script>
export default {
  name: "Saoyisao",
  data() {
    return {
      title: "",
    };
  },
  created() {
    this.title = JSON.parse(this.$route.query.item).title;
  },
  mounted(){
    this.video = document.getElementById("video");
  },
  methods: {
    getMedia() {
        var that=this
        let constraints = {
            video: {width: 500, height: 500},
            audio: true
        };
        /*
        这里介绍新的方法:H5新媒体接口 navigator.mediaDevices.getUserMedia()
        这个方法会提示用户是否允许媒体输入,(媒体输入主要包括相机,视频采集设备,屏幕共享服务,麦克风,A/D转换器等)
        返回的是一个Promise对象。
        如果用户同意使用权限,则会将 MediaStream对象作为resolve()的参数传给then()
        如果用户拒绝使用权限,或者请求的媒体资源不可用,则会将 PermissionDeniedError作为reject()的参数传给catch()
        */
        let promise = navigator.mediaDevices.getUserMedia(constraints);
        promise.then(function (MediaStream) {
          console.log(MediaStream)
          console.log(typeof MediaStream.stop)
          console.log(MediaStream.getTracks())
            that.MediaStreamTrack=typeof MediaStream.stop==='function'?MediaStream:MediaStream.getTracks()[1];
            that.video.srcObject = MediaStream;
            that.video.play();
        }).catch(function (PermissionDeniedError) {
            console.log(PermissionDeniedError);
        })
    },
    takePhoto() {
        var that=this
        //获得Canvas对象
        let canvas = document.getElementById("canvas");
        let ctx = canvas.getContext('2d');
        ctx.drawImage(that.video, 0, 0, 500, 500);
        console.log(that.MediaStreamTrack)
        // that.video.pause();
        that.MediaStreamTrack && that.MediaStreamTrack.stop()
    }
    //获得video摄像头区域
  },
};
</script>

